import React, { useState, useEffect } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import axios from 'axios'
import { MdTag } from 'react-icons/md'
import moment from 'moment'
import { useFormik } from 'formik'
import * as yup from 'yup'
import WaterApiList from '../../../Components/ApiList/WaterApiList'


const WaterDemandPayment = () => {
    const [applicationFullData, setApplicationFullData] = useState()
    const [isLoading, setIsLoading] = useState(false);
    const [demandData, setDemandData] = useState()

    const [demandCalculationData, setDemandCalculationData] = useState()
    const [fromDate, setFromDate] = useState()
    const [uptoDate, setUptoDate] = useState()


    const { id } = useParams()
    const navigate = useNavigate()

    // useSetTitle('Water Demand Payment')


    const { api_waterApprovedApplicationDetails, api_waterListDemand, api_waterDemandCalculation, api_waterDemandPayment, header } = WaterApiList();

    useEffect(() => {
        getApplicationDetail() //Fetch Application Details
        getDemandListDetails() //Fetch All Due Demand List
    }, [id])

    useEffect(() => {
        demandCalculation()
    }, [uptoDate, fromDate])

    const demandCalculation = () => {// Demand Calculation Api called

        if (uptoDate == "") return setDemandCalculationData()
        if (fromDate == "") return setDemandCalculationData()

        setIsLoading(true)

        const payload = {
            "consumerId": id,
            "demandFrom": fromDate,
            "demandUpto": uptoDate
        }

        axios.post(api_waterDemandCalculation, payload, header)
            .then(function (response) {
                console.log('water demand calculation Details', response.data.data)
                setDemandCalculationData(response.data.data)
                setIsLoading(false)
            })
            .catch(function (error) {
                console.log('Error : water calculating demand', error)
                setIsLoading(false)
            })
    }


    const getApplicationDetail = () => {
        setIsLoading(true)
        axios.post(api_waterApprovedApplicationDetails, { id: id }, header)
            .then(function (response) {
                console.log('water Approved Application Details', response.data.data)
                setApplicationFullData(response.data.data)
                setIsLoading(false)
            })
            .catch(function (error) {
                console.log('Error : water Approved Application Details', error)
                setIsLoading(false)
            })
    }

    const getDemandListDetails = () => {
        axios.post(api_waterListDemand, { "ConsumerId": id }, header)
            .then((res) => {
                console.log("Fetched Water Demand List", res.data.data)
                setDemandData(res.data.data)
            })
            .catch((err) => {
                console.log("Exception while fetching Water Demand", err)
            })
    }


    const submitForm = (data) => {

        const payload = {
            'consumerId': id,
            'demandFrom': fromDate,
            'demandUpto': fromDate,
            'amount': demandCalculationData?.totalPayAmount,
        }

        console.log("final submit", payload)

        // return

        axios.post(api_waterDemandPayment, payload, header)
            .then((res) => {
                if (res.data.status) {
                    getDemandListDetails()
                    console.log("Demand Paid Successfully", res)
                } else {
                    console.log("Failed to paid Demand.", res)
                }
            })
            .catch((err) => {
                console.log("Exception while Demand payment", err)
            })

    }


    //Formik Start


    const validationSchema = yup.object({

        monthFrom: yup.string().required('Require Field'),
        monthUpto: yup.string().required('Require Field'),
        // remarks: yup.string().required('Require Field'),


    })
    const formik = useFormik({
        enableReinitialize: true,
        initialValues: {
            monthFrom: '',
            monthUpto: '',
            penalty: '',
            rebate: '',
            advanced: '',
        },

        onSubmit: (values, resetForm) => {
            // console.log("Form Data", values)
            submitForm(values)
        }
        , validationSchema
    })

    const handleChange = (e) => {
        let name = e.target.name
        let value = e.target.value


        { (name == 'monthFrom') && setFromDate(value) }
        { (name == 'monthUpto') && setUptoDate(value) }

    }

    //Formik END


    // Filter Data Demand upto should be greater than demand upto
    const filterDate = demandData?.consumerDemands?.filter(item => moment(item.demand_upto, 'YYYY-MM-DD').format('yy-MM').toString() >= moment(fromDate, 'YYYY-MM-DD').format('yy-MM'));
    // const filterDate =  demandData?.consumerDemands?.filter(item =>  item.demand_upto >= fromDate);

    // console.log("filterDate",fromDate, moment(fromDate, 'YYYY-MM-DD').format('MMyy'))
    // console.log("filterDate", filterDate)
    return (
        <>
            {/* {isLoading && <BarLoader />} */}
            <div className='grid grid-cols-1 md:grid-cols-12 lg:grid-cols-12 gap-2 container mx-auto w-full'>

                <div className="col-span-12 rounded-lg p-4">

                    {/* Basic  details */}
                    <div className='p-4'>
                        <div className=''>
                            <h1 className='px-1 font-semibold font-serif  text-gray-500'><MdTag className="inline" /> Basic Details</h1>
                            <div className='py-6 mt-2 bg-white rounded-lg shadow-xl p-4'>
                                <div className="flex space-x-5 pl-4 ">
                                    <div className='flex-1'>
                                        <div className='font-semibold text-sm'>{applicationFullData?.ulb_name ? applicationFullData?.ulb_name : "N/A"}</div>
                                        <div className='text-gray-500 text-xs'>ULB Name</div>
                                    </div>
                                    <div className='flex-1'>
                                        <div className='font-bold text-sm'>{applicationFullData?.ward_name ? applicationFullData?.ward_name : "N/A"}</div>
                                        <div className='text-gray-500 text-xs'>Ward No.</div>
                                    </div>
                                    <div className='flex-1'>
                                        <div className='font-bold text-sm'>{applicationFullData?.consumer_no ? applicationFullData?.consumer_no : "N/A"}</div>
                                        <div className='text-gray-500 text-xs'>Consumer No</div>
                                    </div>
                                    <div className='flex-1'>
                                        <div className='font-bold text-sm'>{applicationFullData?.connection_through ? applicationFullData?.connection_through : "N/A"}</div>
                                        <div className='text-gray-500 text-xs'>Connection Through</div>
                                    </div>
                                    {applicationFullData?.connection_through == 'SAF' ?
                                        <div className='flex-1'>
                                            <div className='font-semibold text-md'>{applicationFullData?.saf_no ? applicationFullData?.saf_no : "N/A"}</div>
                                            <div className='text-gray-500 text-xs'>SAF No</div>
                                        </div> :
                                        <div className='flex-1'>
                                            <div className='font-semibold text-md'>{applicationFullData?.holding_no ? applicationFullData?.holding_no : "N/A"}</div>
                                            <div className='text-gray-500 text-xs'>Holding No</div>
                                        </div>
                                    }
                                    <div className='flex-1'>
                                        <div className='font-semibold text-md'>{applicationFullData?.owner_char_type ? applicationFullData?.owner_char_type : "N/A"}</div>
                                        <div className='text-gray-500 text-xs'>Ownership Type</div>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>


                    <>
                        <div className='mt-5 p-4'>
                            <h1 className='px-1 font-semibold font-serif text-gray-500'><MdTag className="inline" />Due Demand Details</h1>

                            <table className='min-w-full leading-normal mt-2 bg-white rounded-lg shadow-xl p-4'>
                                <thead className='font-bold text-left text-sm border text-gray-800 bg-white'>
                                    <tr>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">#</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">Demand From</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">Demand Upto</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">Demand Amount</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">Penalty</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">Connection Type</th>
                                        <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">Payable Amount </th>
                                    </tr>
                                </thead>
                                <tbody className="text-sm">

                                    <>
                                        {demandData?.consumerDemands?.map((items, i) => (
                                            <tr className="bg-white shadow-xl border-b border-gray-200">
                                                <td className="px-2 py-2 text-left text-gray-500 text-xs">{i + 1}</td>
                                                <td className="px-2 py-2 text-left text-gray-500 text-xs">{items?.demand_from ? moment(items?.demand_from, 'YYYY-MM-DD').format('DD-MMM-yy') : "N/A"}</td>
                                                <td className="px-2 py-2 text-left text-gray-500 text-xs">{items?.demand_upto ? moment(items?.demand_upto, 'YYYY-MM-DD').format('DD-MMM-yy') : "N/A"}</td>
                                                <td className="px-2 py-2 text-left text-gray-500 text-xs">{items?.balance_amount ? items?.balance_amount : "N/A"}</td>
                                                <td className="px-2 py-2 text-left text-gray-500 text-xs">{items?.penalty ? items?.penalty : "N/A"}</td>
                                                <td className="px-2 py-2 text-left text-gray-500 text-xs">{items?.connection_type ? items?.connection_type : "N/A"}</td>
                                                <td className="px-2 py-2 text-left text-gray-500 text-xs">{items?.amount ? '₹ ' + items?.amount : "N/A"}</td>

                                            </tr>
                                        ))}
                                    </>
                                </tbody>
                            </table>
                        </div>

                    </>


                    <div className="col-span-12 rounded-lg p-4">
                        <div className='py-6 mt-2 bg-white rounded-lg shadow-xl border border-y-gray-200 p-4'>
                            <form onSubmit={formik.handleSubmit} onChange={handleChange} className="p-4 relative">

                                <div className="grid grid-cols-12">

                                    {/* Forms */}

                                    <div className="form-group mb-6 col-span-12 md:col-span-6 md:px-4">
                                        <label className="form-label inline-block mb-1 text-gray-600 text-sm font-semibold"><small className=" mt-1 text-sm font-semibold text-red-600 inline ">*</small>Select Month From</label>
                                        <select {...formik.getFieldProps('monthFrom')} type="text" className="form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none placeholder-gray-300 shadow-md"
                                        >
                                            <option value="">Select</option>
                                            {demandData?.consumerDemands?.map((item, index) => (
                                                <option key={index} value={item.demand_from}>{moment(item.demand_from, 'YYYY-MM-DD').format('MMM-yy')}
                                                </option>
                                            ))}
                                        </select>
                                        <span className="text-red-600 absolute text-xs">{formik.touched.monthFrom && formik.errors.monthFrom ? formik.errors.monthFrom : null}</span>
                                    </div>
                                    <div className="form-group mb-6 col-span-12 md:col-span-6 md:px-4">
                                        <label className="form-label inline-block mb-1 text-gray-600 text-sm font-semibold"><small className=" mt-1 text-sm font-semibold text-red-600 inline ">*</small>Select Month Upto</label>
                                        <select {...formik.getFieldProps('monthUpto')} type="text" className="form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none placeholder-gray-300 shadow-md"
                                        >
                                            <option value="">Select</option>
                                            {filterDate?.map((item, index) => (
                                                <option key={index} value={item.demand_upto}>{moment(item.demand_upto, 'YYYY-MM-DD').format('MMM-yy')}</option>
                                            ))}
                                        </select>
                                        <span className="text-red-600 absolute text-xs">{formik.touched.monthUpto && formik.errors.monthUpto ? formik.errors.monthUpto : null}</span>
                                    </div>
                                    <div className="form-group mb-6 col-span-12 md:col-span-6 md:px-4">
                                        <label className="form-label inline-block mb-1 text-gray-600 text-sm font-semibold">Total Demand</label>
                                        <input {...formik.getFieldProps('amount')} value={demandCalculationData?.totalDemand || 0} disabled type="text" className="form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:border-blue-600 focus:outline-none placeholder-gray-300 shadow-md bg-slate-200" />
                                        <span className="text-red-600 absolute text-xs">{formik.touched.amount && formik.errors.amount ? formik.errors.amount : null}</span>
                                    </div>
                                    <div className="form-group mb-6 col-span-12 md:col-span-6 md:px-4">
                                        <label className="form-label inline-block mb-1 text-gray-600 text-sm font-semibold">Penalty</label>
                                        <input {...formik.getFieldProps('penalty')} value={demandCalculationData?.totalPenalty || 0} disabled type="text" className="form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:border-blue-600 focus:outline-none placeholder-gray-300 shadow-md bg-slate-200" />
                                        <span className="text-red-600 absolute text-xs">{formik.touched.penalty && formik.errors.penalty ? formik.errors.penalty : null}</span>
                                    </div>
                                    <div className="form-group mb-6 col-span-12 md:col-span-6 md:px-4">
                                        <label className="form-label inline-block mb-1 text-gray-600 text-sm font-semibold">Rebate</label>
                                        <input {...formik.getFieldProps('rebate')} value={demandCalculationData?.totalRebate || 0} disabled type="text" className="form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:border-blue-600 focus:outline-none placeholder-gray-300 shadow-md bg-slate-200" />
                                        <span className="text-red-600 absolute text-xs">{formik.touched.rebate && formik.errors.rebate ? formik.errors.rebate : null}</span>
                                    </div>
                                    <div className="form-group mb-6 col-span-12 md:col-span-6 md:px-4">
                                        <label className="form-label inline-block mb-1 text-gray-600 text-sm font-semibold">Advanced</label>
                                        <input {...formik.getFieldProps('advanced')} value={demandCalculationData?.totalAdvance || 0} disabled type="text" className="form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:border-blue-600 focus:outline-none placeholder-gray-300 shadow-md bg-slate-200" />
                                        <span className="text-red-600 absolute text-xs">{formik.touched.advanced && formik.errors.advanced ? formik.errors.advanced : null}</span>
                                    </div>

                                    <div className="form-group mb-4 col-span-12 md:col-span-6 md:px-4">
                                        <p>Remain Advance : <span className='text-orange-700 font-semibold'>{demandCalculationData?.remaningAdvanceAmount || 0}</span></p>
                                    </div>
                                    <div className="form-group mb-4 col-span-12 md:col-span-6 md:px-4">
                                        <p> Payable Amount : <span className='text-green-700 font-semibold'>{demandCalculationData?.totalPayAmount ? '₹' + demandCalculationData?.totalPayAmount : 0}</span></p>
                                    </div>

                                    <div className="col-span-12 grid grid-cols-2 mt-10">

                                        <div className=''></div>
                                        <div className='md:pl-10 text-right space-x-5'>
                                            <button type='button' onClick={() => navigate(-1)} className=" px-6 py-2.5 bg-sky-600 text-white font-medium text-xs leading-tight  rounded shadow-md hover:bg-sky-700 hover:shadow-lg focus:bg-sky-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-indigo-800 active:shadow-lg transition duration-150 ease-in-out cursor-pointer">Back</button>
                                            <button type='submit' className=" px-6 py-2.5 bg-indigo-600 text-white font-medium text-xs leading-tight  rounded shadow-md hover:bg-indigo-600 hover:shadow-lg focus:bg-indigo-700 focus:shadow-lg focus:outline-none focus:ring-0 active:shadow-lg transition duration-150 ease-in-out cursor-pointer">Pay Tax</button>
                                        </div>

                                    </div>
                                </div>


                            </form>


                        </div>
                    </div>
                </div>

            </div>

        </>
    )
}

export default WaterDemandPayment